@import "../../shared/styles/mixin.scss";

.header {
  position: fixed;
  z-index: 10;
  top: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.88);
  display: flex;
  -webkit-box-shadow: 0px 6px 7px 0px rgba(0, 0, 0, 0.36);
  -moz-box-shadow: 0px 6px 7px 0px rgba(0, 0, 0, 0.36);
  box-shadow: 0px 6px 7px 0px rgba(0, 0, 0, 0.36);
  overflow: hidden;

  .header_container {
    width: 85%;
    height: 70px;
    // background: red;
    margin: auto;
    display: grid;
    grid-template-columns: 12% 76% 12%;
    grid-gap: 0 10px;

    .image_content {
      display: flex;
      align-items: center;
      // justify-content: center;

      img {
        cursor: pointer;
        width: 90px;
      }
    }

    .navigation {
      // margin: auto;
      // height: 81px;
      // margin-top: 20px;
      list-style: none;
      display: flex;
      // max-width: 100%;

      .item_li {
        cursor: pointer;
        padding: 0 15px;

        &:first-child {
          padding-left: 0 !important;
        }

        &:last-child {
          padding-right: 0 !important;
        }

        a {
          text-decoration-line: none;
          color: #a29c9c;
          transition: color 0.4s ease 0s;
          padding: 0 12px;
          height: 100%;
          display: flex;
          align-items: center;

          &:hover {
            color: black;
          }
        }
      }

    }

    .icon_buttons {
      font-size: 1.3em;
      display: flex;
      align-items: center;
      justify-content: flex-end;

      svg {
        padding: 0.5em;
        box-sizing: content-box;
        cursor: pointer;
        margin-left: 0.7em;
      }
    }
  }

  .pasarela_products {
    z-index: 3;
    border-top: 3px solid white;
    background-color: rgba(255, 255, 255, 0.88);
    position: absolute;
    width: 100%;
    top: 70px;
    transition: all 0.4s ease;
    -webkit-box-shadow: 0px 6px 7px 0px rgba(0, 0, 0, 0.36);
    -moz-box-shadow: 0px 6px 7px 0px rgba(0, 0, 0, 0.36);
    box-shadow: 0px 6px 7px 0px rgba(0, 0, 0, 0.36);
    overflow: hidden;
    display: flex;

    .pasarela_content {
      @include responsive-layout;
      display: flex;
      align-items: center;

      .new_arrivals {
        min-width: 210px;
        list-style: none;
        line-height: 1.4;
        color: #a29c9c;

        a {
          font-weight: bold;
          font-style: italic;
          // color: #a29c9c;
          color: black;
        }

        li {
          transition: 0.3s;
          cursor: pointer;

          &:hover {
            color: black;
          }
        }
      }

      .images_list {
        list-style: none;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        color: rgb(97, 97, 97);
        font-size: 0.8em;
        // line-height: 2;

        li {
          // border-right: 1px solid;
          padding: 0 0.4em;
          text-align: center;
          font-weight: bold;
          transition: 0.3s;
          cursor: pointer;

          span {
            margin-top: 1em;
          }

          img {
            width: 95%;
            margin: 0 1em;
          }

          &:hover {
            color: black;
          }
        }
      }
    }
  }

  // .nav_list {
  //   margin: auto;
  //   height: 81px;
  //   margin-top: 20px;
  //   list-style: none;
  //   display: flex;
  //   max-width: 100%;

