  //       align-items: center;

  //       &:hover {
  //         color: black;
  //       }
  //     }
  //   }
  // }
}

.lateral_menu {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 200px;
  background: #00000069;
  transition: all 0.9s;
}

// Mobile
@media screen and (max-width: 599px) {
  .header {
    position: relative;

    .nav_list {
      margin-top: 0;
      flex-direction: column;
      align-items: center;
      height: auto !important;

      li {
        padding: 0.5em 0 !important;
      }
    }

    .pasarela_products {
      display: none !important;
    }
  }
}

